@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,700;1,400&display=swap);
* {
	margin: 0;
	padding: 0;
	text-align: center;
	font-family: "Montserrat", sans-serif;
}

:root {
	--white: #f8f8f8;
	--light: #f8f8f8;
	--head-light: #f8f8f8;
	--grey: #232323;
	--gradient-grey: #232323;
	--skill-grey: #272727;
	--theme: #8148eb;
	--dark: #191919;
	--purple: #8e61e2;
	--logo-bg: none;
	--light-purple: #bd6df2;
	--shadow: none;
	--icon-bg: #5555556e;
	--gradient: linear-gradient(134deg, #836af0 0%, #8e49dc 99%);
	--gradient-light: linear-gradient(99deg, #836af0 0%, #8e49dc 130%);
	--input: #f0f0f0;
	--filter: brightness(20%);
}

.light {
	--light: #8949dc;
	--head-light: #313131;
	--grey: #f8f8f8;
	--gradient-grey: var(--gradient);
	--skill-grey: #dfc7ff;
	--dark: #f0ecec;
	--logo-bg: #8e61e2;
	--light-purple: #f8f8f8;
	--shadow: -1px 3px 12px 0px #8b8b8b21;
	--icon-bg: rgba(209, 209, 209, 0.233);
	--input: #eeeeee;
	--filter: contrast(15%);
}

#root {
	color: #f8f8f8;
	color: var(--light);
	background: #191919;
	background: var(--dark);
	min-height: 100vh;
	transition: 300ms ease-in-out;
}

html {
	scroll-behavior: smooth;
}

input {
	text-align: left;
	color: #f8f8f8;
	color: var(--head-light)
}

button:disabled {
	cursor: not-allowed;
	opacity: 0.5;
}

::-webkit-scrollbar {
    width: 10px;
    height: 8px;
}

::-webkit-scrollbar-track {
    background: #232323;
    background: var(--grey)
}

::-webkit-scrollbar-thumb {
    background: #8e61e2;
    background: var(--purple);
    border-radius: 5px;
}

.link{
	text-decoration: none;
	color: var(--light);
}

.header {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	background: var(--grey);
	position: sticky;
	top: 0;
	z-index: 100;
}

.headerLogo {
	object-fit: contain;
	margin: 0 20px;
}

.headerSearchContainer {
	display: flex;
	flex: 1 1;
	align-items: center;
}

.headerSearchInput {
	height: 12px;
	padding: 10px;
	margin: 0 10px;
	border: none;
    width: 100%;
    border-radius: 24px;
    background: var(--dark);
    color: var(--head-light);
}

.headerSearchIcon {
	padding: 5px 8px;
    margin-right: 10px;
    line-height: 22px;
    display: block;
    text-align: center;
    color: var(--white);
    background: var(--gradient);
    border-radius: 24px;
    width: 22px;
}

.headerAvatar{
	border-radius: 50%;
	width: 25px;
}

.headerOptionLineOne {
	font-size: 10px;
}

.headerOptionLineTwo {
	font-size: 13px;
	font-weight: 800;
}

.headerOptionBasket {
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 5px 6px;
	font-size: 20px
}

.headerNavContainer {
	display: flex;
	justify-content: space-evenly;
}

.headerOption {
	display: flex;
	flex-direction: column;
	margin-left: 10px;
	margin-right: 10px;
}

.headerTheme {
	cursor: pointer;
	color: var(--white);
	background: var(--gradient);
	margin: 10px;
	float: right;
	border-radius: 50%;
	font-size: 1.2em;
	font-weight: 900;
    transition: 200ms ease-in-out;
    z-index: 10;
}

.headerTheme i {
	padding: 7px 7px;
}

.headerSearchIcon:hover, .headerOption:hover, .headerOptionBasket:hover {
	cursor: pointer;
}


@media screen and (max-width: 768px) {
	.headerNavContainer, .headerSearchContainer, .headerTitle {
		width: 100vw;
		flex-direction: row;
		margin: 5px 0;
	}

	.headerLogo {
		object-fit: contain;
		margin: 0 20px;
		font-size: 1.7em;
	}

	.break {
		flex-basis: 100%;
		height: 0;
	}

	.headerTheme {
		position: absolute;
		top: 0;
		right: 0;
	}
}


.login {
	min-height: calc(100vh - 113px);
	background-color: var(--dark);
	margin: 20px 0;
	display: grid;
	place-items: center;
}

.loginContainer {
    max-width: 400px;
	padding: 20px;
	text-align: center;
	background-color: var(--grey);
	border-radius: 15px;
	box-shadow: 0 4px 5px rgba(0, 0, 0, 0.2);
}

.loginContainer button {
    border: none;
    width: 100%;
    margin: 10px 0;
	border-radius: 40px;
	background-color: var(--purple);
	padding: 10px;
	font-size: 18px;
	color: var(--white);
	cursor: pointer;
}

.loginContainer .loginRegisterButton {
    background-color: var(--input);
    color: var(--theme);
}

.loginContainer > form > h5 {
    margin: 5px;
    text-align: left;
}

.loginContainer > form > input {
	height: 30px;
    margin-bottom: 10px;
    padding: 5px;
	border: 0px;
	color: var(--light);
	background-color: var(--dark);
	width: calc(100% - 10px);
}

.loginContainer > p {
	margin-top: 15px;
	font-size: 12px;
}
.post {
    width: 100%;
    max-width: 500px;
    background-color: var(--grey);
    border-radius: 5px;
    border: 1px solid var(--icon-bg) ;
    margin-bottom: 50px;
    box-shadow: 0 4px 5px rgba(0, 0, 0, 0.2);
}

.post a{
    text-decoration: none;
    color: var(--light);
}

.postHeader {
    display: flex;
    align-items: center;
    padding: 10px
}

.avatar {
    border-radius: 50%;
    width: 40px;
    margin: 5px 10px 5px 5px
}

.postImage {
    width: 100%;
    max-width: 500px;
    max-height: 700px;
    object-fit: cover;
    border-top: 1px solid var(--icon-bg) ;
    border-bottom: 1px solid var(--icon-bg) ;
}

.postInteractionBar{
    padding: 5px 10px;
    text-align: left;
    border-bottom: 1px solid var(--icon-bg) ;
}

.postInteractionItem{
    padding: 0px 20px 0 0;
    font-size: 24px;
}

.postHeart{
    color: var(--purple);
    cursor: pointer;
}

.postComments {
    padding: 10px;
    text-align: left;
    font-size: 14px;
    text-align: left;
    word-wrap: break-word;
}

.postCaption {
    text-align: left;
    padding: 0px;
}

.postMoreComments {
    padding-top: 10px;
}

.postMoreComments:hover {
    text-decoration: underline;
}

.postCommentsInput {
    display: flex;
    border-top: 1px solid var(--icon-bg) ;
}

.message {
    text-align: center;
    width: 100%;
    padding: 10px;
}

.postComment {
    flex: 1 1;
    border: none;
    padding: 10px;
    background-color: var(--grey);
}

.postCommentButton {
    flex: 0 1;
    cursor: pointer;
    background-color: var(--grey);
    border: none;
    color: var(--purple);
    padding: 10px;
}
.singlePostContainer {
    display: grid;
    justify-content: center;
    align-content: center;
    min-height: calc(100vh - 113px);
}



.singlePost {
    width: -webkit-fit-content;
    width: fit-content;
    /* max-height: 537px; */
    display: flex;
    flex-direction: row;
    background-color: var(--grey);
    border-radius: 5px;
    border: 1px solid var(--icon-bg) ;
    margin: 40px;
    box-shadow: 0 4px 5px rgba(0, 0, 0, 0.2);
}

.singlePostImage {
    width: 100%;
    min-width: 100%;
    max-width: 500px;
    max-height: 700px;
    object-fit: cover;
    border-radius: 5px 0 0 5px;
    border-top: 1px solid var(--icon-bg) ;
    border-bottom: 1px solid var(--icon-bg) ;
}

.report{
    border-radius: 8px;
    padding: 10px;
    color: red;
    background-color: white;
    font-style: italic;
}

.singlePostRight{
    width: 300px;
}

.singlePostComments{
    min-height: calc(100% - 163px);
    max-height: 400px;
    overflow-y: auto;
}

.commentLink{
    color: var(--light);
    text-decoration: none;
}

.commentLink:hover{
    text-decoration: underline;
}

.singlePostInteractionBar{
    border-bottom: none;
    border-top: 1px solid var(--icon-bg) ;
}


@media screen and (max-width: 900px) {
	.singlePostContainer {
        min-height: calc(100vh - 149px);
    }

    .singlePost {
        flex-direction: column;
        max-height: unset;
    }

    .singlePostImage {
        border-radius: 5px 5px 0 0;
        min-width: unset;
        max-height: 450px;
        max-width: 450px;
    }

    .singlePostRight{
        width: unset;
    }
}


.profileContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    margin-top: 20px;
}

.profileAvatar{
    border-radius: 50%;
    width: 100px;
}

.profileName {
    display: inline;
    padding: 10px;
}
.upload {
    position: fixed;
	top: calc(50% + 30px);
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
    width: 560px;
    max-height: calc(100vh - 210px);
    overflow-y: auto;
	padding: 30px;
	background-color: var(--grey);
	display: none;
	align-items: center;
    border-radius: 10px;
    z-index: 2;
}

.uploadContent {
	width: 100%;
	max-width: 500px;
	margin: 0 auto;
	display: flex;
	flex-direction: column;
}

.uploadPreview {
	text-align: center;
	width: 100%;
}

.uploadProgress {
	width: calc(100%);
	background: var(--dark);
	margin: 10px 0;
}

.uploadCaption {
	margin: 10px 0;
	background-color: var(--dark);
	border-radius: 5px;
	padding: 10px;
	border: none;
}

.uploadButtons {
	display: flex;
}

input[type="file"] {
	display: none;
}

.customFileUpload {
	display: inline-block;
	cursor: pointer;
	width: -webkit-fit-content;
	width: fit-content;
	padding: 10px;
	background-color: var(--dark);
	border-radius: 5px;
	margin: 10px 0;
	font-size: 0.8em;
	flex: 1 1;
}

.fileName {
	flex: 1 1;
	margin: auto 0;
	font-size: 12px;
}

.trashBtn {
	background: var(--gradient);
	flex: 0 1;
	margin: auto 0;
	padding: 5px 8px;
	width: -webkit-fit-content;
	width: fit-content;
	border-radius: 50%;
	color: var(--white);
	cursor: pointer;
}

.customFileUpload i {
	font-size: 14px;
}

.postButton {
	flex: 1 1;
	cursor: pointer;
	margin: 10px 0;
	padding: 10px;
	background-color: var(--dark);
	border-radius: 5px;
	border: none;
	color: var(--purple);
}

.closeBtn{
    background: var(--gradient);
	margin: 10px ;
	padding: 5px 9px;
	width: -webkit-fit-content;
	width: fit-content;
	border-radius: 50%;
	color: var(--white);
    cursor: pointer;
    position: absolute;
    top: 0;
    right: 0;
}

.addPostBtn{
    background: var(--gradient);
	margin: 20px ;
	padding: 10px 12px;
	width: -webkit-fit-content;
	width: fit-content;
	border-radius: 50%;
	color: var(--white);
    cursor: pointer;
	position: sticky;
	float: right;
	margin-top: -60px;
    bottom: 20px;
    right: 0px;
}

.dropdown {
	text-align: right;
	color: black;
}

@media screen and (max-width: 768px) {
	.upload {
        width: calc(100% - 40px);
        height: calc(100vh - 40px);
        top: calc(50%);
        max-height: unset;
        padding: 20px;
		min-width: 220px;
	}
}

.footer {
    height: 60px;
    width: 100%;
    background: var(--grey);
    display: flex;
    justify-content: center;
    flex-direction: column;
    z-index: 6;
}

.footer p {
    line-height: 24px;
    font-size: 16px;
    padding: 0;
    color: var(--head-light);
    text-align: center;
}

.footer a {
    color: var(--purple);
    text-decoration: none;
}
.posts {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.filter {
  background-color: black;
  display: none;
  width: 100%;
  height: 100vh;
  filter: alpha(opacity=50);
	opacity: 0.6;
  z-index: 1;
  position: fixed;
  top: 0;
  left: 0;
}
